.introduce {
  border-bottom: .125rem solid #3a3f47;
  display: flex;
  padding: 20vh 2.5vw 15.5vh;
}

.introduce .text {
  color: #FFFFFF;
  width: 50vw;
}

.introduce .text h2 {
  font-size: 4rem;
  margin: 0;
  padding-bottom: 4vh;
}

.introduce .text .color {
  color: #0097a7;
}

.introduce .text .sub {
  color: #FFFFFF;
  font-size: 2rem;
  margin: 0;
  padding-bottom: 1vh;
}

.introduce .text p {
  color: #d1d5da;
  font-size: 1.125rem;
  text-align: justify;
}

.introduce .text .media {
  display: flex;
  gap: 1vw;
}

.introduce .text .media a svg {
  color: #FFFFFF;
}

.introduce .text .media a svg:hover {
  color: #0097a7;
}

.introduce .img {
  display: flex;
  justify-content: end;
  width: 50vw;
}

.introduce .img img {
  border: 0.25rem solid #4e5d6c;
  border-radius: 4rem;
  max-height: 50vh;
}